import React, { useState, useEffect, useCallback } from 'react';
import { Link } from 'react-router-dom'; // Import Link for Job Number
import { getWeeklyShipments } from '../services/apiService';
import './WeeklyShipmentsPage.css';

const WeeklyShipmentsPage = () => {
    const [shipments, setShipments] = useState([]);
    const [dateRange, setDateRange] = useState(null);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState('');
    const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);

    const fetchWeeklyData = useCallback(async (date) => {
        setIsLoading(true);
        try {
            const response = await getWeeklyShipments(date);
            setShipments(response.data.shipments);
            setDateRange(response.data.date_range);
            setError('');
        } catch (err) {
            setError('Failed to fetch weekly shipment data.');
            console.error(err);
        } finally {
            setIsLoading(false);
        }
    }, []);

    useEffect(() => {
        fetchWeeklyData(selectedDate);
    }, [fetchWeeklyData, selectedDate]);

    const handlePrint = () => {
        window.print();
    };

    return (
        <div className="weekly-shipments-page card">
            <div className="report-header no-print">
                <div className="report-title">
                    <h2>Weekly Shipments Report</h2>
                    {dateRange && (
                        <p className="date-range-display">
                            For the week of {dateRange.start} to {dateRange.end}
                        </p>
                    )}
                </div>
                <div className="week-selector">
                    <label htmlFor="week-picker">Select a Week:</label>
                    <input
                        type="date"
                        id="week-picker"
                        value={selectedDate}
                        onChange={e => setSelectedDate(e.target.value)}
                    />
                </div>
                <button onClick={handlePrint} className="print-button">Print Report</button>
            </div>

            <div className="report-content">
                {isLoading && <p>Loading report...</p>}
                {error && <p className="error-message">{error}</p>}
                
                {/* --- RENDER A TABLE INSTEAD OF CARDS --- */}
                <table className="report-table">
                    <thead>
                        <tr>
                            <th>Customer</th>
                            <th>Job Number</th>
                            <th>Shipping Date</th>
                            <th>Total Units</th>
                            <th>Shipped Units (by Type)</th>
                            <th>Status</th>
                        </tr>
                    </thead>
                    <tbody>
                        {shipments.length > 0 ? (
                            shipments.map(shipment => (
                                <tr key={shipment.id}>
                                    <td>{shipment.customer_name}</td>
                                    <td>
                                        <Link to={`/shipment/${shipment.id}`}>{shipment.job_number}</Link>
                                    </td>
                                    <td>{shipment.shipping_date}</td>
                                    <td className="total-units-cell">{shipment.total_units}</td>
                                    <td>
                                        {shipment.shipped_units_summary.length > 0 ? (
                                            <ul className="unit-summary-list">
                                                {shipment.shipped_units_summary.map((summary, index) => (
                                                    <li key={index}>{summary.model_type} ({summary.count})</li>
                                                ))}
                                            </ul>
                                        ) : (
                                            'N/A'
                                        )}
                                    </td>
                                    <td>
                                        <span className={`status-badge ${shipment.status.toLowerCase().replace(' ', '-')}`}>
                                            {shipment.status}
                                        </span>
                                    </td>
                                </tr>
                            ))
                        ) : (
                            !isLoading && (
                                <tr>
                                    <td colSpan="6">No shipments found for the current week.</td>
                                </tr>
                            )
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export default WeeklyShipmentsPage;